/* Add.css */
.admin-add {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    background-color: #f9f9f9;
    height: 90vh;
    overflow: auto;
    box-sizing: border-box;
    width: 100%;
    margin-bottom: 70px;
}

.admin-add form {
    background: #fff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    max-width: 500px;
    width: 100%;
    box-sizing: border-box;
}

.tittle {
    text-align: center;
    padding: 20px;
    font-size: 18px;
    font-weight: bold;
    color: #333;
}

.flex-col {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
}

.add-img-upload label {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 120px;
    height: 120px;
    background-color: #f0f0f0;
    border: 2px dashed #ccc;
    border-radius: 8px;
    margin: 0 auto 10px;
    transition: border-color 0.3s;
}

.add-img-upload label:hover {
    border-color: #007bff;
}

.add-img-upload img {
    max-width: 100%;
    max-height: 100%;
    object-fit: cover;
    border-radius: 8px;
}

.flex-col input,
.flex-col select {
    width: 100%;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 14px;
    transition: border-color 0.3s;
    box-sizing: border-box;
}

.flex-col input:focus,
.flex-col select:focus {
    border-color: #007bff;
    outline: none;
}

.flex-col input::placeholder {
    color: #aaa;
}

.add-category-price {
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
}

.add-category, .add-price, .add-offerPrice {
    flex: 1;
    min-width: 140px;
}

.flex-col button.add-btn {
    background-color: #007bff;
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
    font-weight: bold;
    transition: background-color 0.3s, transform 0.2s;
    width: 100%;
    box-sizing: border-box;
}

.flex-col button.add-btn:hover {
    background-color: #0056b3;
    transform: scale(1.02);
}

/* Responsive Design */
@media (max-width: 768px) {
    .admin-add {
        width: 100%;
        padding: 15px;
    }

    .admin-add form {
        padding: 15px;
    }

    .add-category-price {
        flex-direction: column;
    }
}

@media (max-width: 480px) {
    .admin-add {
        padding: 10px;
    }

    .flex-col input,
    .flex-col select {
        font-size: 12px;
        padding: 10px;
    }

    .flex-col button.add-btn {
        font-size: 14px;
        padding: 10px 15px;
    }
}
